html::before {
    background-color: $theme-bg-3;
    bottom: 0;
    content: "";
    display: block;
    left: 35%;
    position: fixed;
    right: 0;
    top: 0;
    z-index: 0;
} 


body { 
    background-color: white;
    color: #222; 
    font: 62.5% franklin-gothic-urw, -apple-system, BlinkMacSystemFont, sans-serif; 
    line-height: 1.5;
    width: 1250px;

    display: grid;
    grid-template-columns: repeat(10, 100px);
    column-gap: 25px;
}


header {
    grid-column: 1/10;
    display: grid;
    grid-template-columns: repeat(10, 100px);
    column-gap: 25px;
}

main {
    grid-column: 1/10;
}

nav {
    grid-column: 5/10;
}


h1,
.text-size-xxl {
    font-size: $font-size-xxl;
    font-weight: 700;
    letter-spacing: -3px;
    line-height: 1;
}


.text-size-xl {
    font-size: $font-size-xl;
    font-weight: 700;
    letter-spacing: -2px;
    line-height: 1;
}


.text-size-l {
    font-size: $font-size-l;
    font-weight: 700;
    letter-spacing: -1px;
    line-height: 1;
}


.text-size-s {
    font-size: $font-size-s;
    line-height: 1.5;
}


h4 {
    font-size: $font-size-m;
    font-weight: 700;
    margin-top: 10px;
    line-height: 1.1;
}


.text-style-5 {
    color: $color-text-light;
    font-size: $font-size-s;
    letter-spacing: 5px;;
    line-height: 1.5;
    margin-top: 15px;
    text-transform: uppercase;
}


.text-style-4 {
    font-size: $font-size-s;
    line-height: 1.5;
    margin-top: 15px;
}


.type-serif {
    font-family: ff-scala-web, serif;
    font-weight: 400;
    font-style: normal;
}


.u-mar-left--more {
    transform: translateX(600px);
    width: 650px;
}


.u-mar-left--less {
    transform: translateX(100px);
}

 

.c-entry {
    margin-top: 100px;
}



.c-entry__blurb {
    border-left: 6px double $color-grey-lighter;
    display: block;
    margin-top: 15px;
    padding-left: 50px;
}


.c-name {
    color: $color-text-lighter;
    display: inline-block;
    font-size: $font-size-m;
    font-weight: bold;
    grid-column-start: 1;
    grid-column-end: 5;
    position: relative;
    text-transform: uppercase;

    &::before {
        content: "";
        display: block;
        //border-bottom: 5px solid $color-text-light;
        position: absolute;
        left: -9999px;
        right: 0;
    }
}



.c-billboard {
    background: #fff;
    grid-column-start: 1;
    grid-column-end: 8;
    padding: 200px 100px;
    margin: 0 0 100px -100px;
    position: relative;
    z-index: 9;
}



.c-nav__link {
    align-items: center;
    background-color: $theme-bg-1;
    display: flex;
    justify-content: center;
    height: 100px;
    width: 100px;

    &.is-active {
        .c-nav__icon {
            fill: $color-grey-darker;
        }
    }
}

.c-nav__icon {
    fill: $color-text-light;
}


.c-wrapper {
    position: relative;
    display: grid;
    grid-template-columns: repeat(5, 150px);
    grid-column-gap: 75px;
    grid-row-gap: 75px;
    width: 1250px;
    z-index: 2;
}

.c-book__cover {
    //border: 1px solid $color-grey-lighter;
    box-shadow: 0 5px 20px rgba(black, .15);
    width: 150px;
}